import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class CoreService {

  constructor() { }


  generateColors(numColors: number) {
    const colors = [];
    for (let i = 0; i < numColors; i++) {
      const hue = (i * 137.5) % 360; // Spread colors across the color wheel
      const color = 'hsla(' + hue + ', 70%, 50%, 1)';
      colors.push(color);
    }
    return colors;
  }
}
