import { Component, EventEmitter, Input, Output } from '@angular/core';
import dayjs, { Dayjs } from 'dayjs';

@Component({
  selector: 'app-date-range-input',
  templateUrl: './date-range-input.component.html',
  styleUrls: ['./date-range-input.component.scss']
})
export class DateRangeInputComponent {
  @Output() datesUpdated = new EventEmitter();

  @Input() value:any;
  @Output() valueChange:any = new EventEmitter();

  dropsDown = 'down';
  dropsUp = 'up';
  opensRight = 'right';
  opensCenter = 'center';
  opensLeft = 'left';
  locale = {
    firstDay: 1,
    startDate: dayjs().startOf('day'),
    endDate: dayjs().endOf('day'),
    format: 'YYYY-MM-DD',
    applyLabel: 'Apply',
    cancelLabel: 'Cancel',
    fromLabel: 'From',
    toLabel: 'To',
    displayFormat: 'YYYY-MM-DD',
  };

  invalidDates: Dayjs[] = [];

  ranges: any = {
    '3 Month': [
      dayjs().subtract(89, 'days').startOf('day'),
      dayjs().endOf('day'),
    ],
    '6 Month': [
      dayjs().subtract(179, 'days').startOf('day'),
      dayjs().endOf('day'),
    ],
    '1 Year': [
      dayjs().subtract(364, 'days').startOf('day'),
      dayjs().endOf('day'),
    ],
  };

  onUpdate(e:any) {
    console.log(e,'evento');
    
    this.datesUpdated.emit(e);
    this.valueChange.emit(e);
  }

  isCustomDate = (date: Dayjs) => {
    return date.month() === 0 || date.month() === 6 ? 'mycustomdate' : false;
  };

  isInvalidDate = (m: Dayjs) => {
    return this.invalidDates.some((d) => d.isSame(m, 'day'));
  };

}
