import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AdminProfileComponent } from './admin-profile/admin-profile.component';
import { AdminComponent } from './admin.component';
import { AdminDashboardComponent } from './Admin-Dashboard/admin-dashboard/admin-dashboard.component';
import { AveFileTatTableComponent } from './Admin-Dashboard/ave-file-tat-table/ave-file-tat-table.component';
import { TechTeamDashTableComponent } from './Admin-Dashboard/tech-team-dash-table/tech-team-dash-table.component';
import { AdminRootGuard } from '../guards/adminRoot.guard';
import { AdminGuard } from '../guards/admin.guard';


const routes: Routes = [
  { path: '', 
    component: AdminComponent, 
    children:[
      {path:'', canActivate:[AdminGuard], component:AdminDashboardComponent , data: { title: 'Home' }},
      {path:'profile', canActivate:[AdminGuard], component:AdminProfileComponent},
      {path:'AFTT', canActivate:[AdminGuard], component:AveFileTatTableComponent},
      {path:'techTeamDashTable',canActivate:[AdminGuard], component:TechTeamDashTableComponent},
      {path: 'role', canActivateChild:[AdminRootGuard], loadChildren: () => import('./role-management/role-management.module').then(m => m.RoleManagementModule) },
      {path: 'customer', canActivateChild:[AdminRootGuard], loadChildren: () => import('./customer-management/customer-management.module').then(m => m.CustomerManagementModule) },
      {path: 'permission', canActivateChild:[AdminRootGuard], loadChildren: () => import('./permission/permission.module').then(m => m.PermissionModule) },
      {path: 'template', canActivateChild:[AdminRootGuard], loadChildren: () => import('../admin/template/template.module').then(m => m.TemplateModule) },
      {path: 'master', canActivateChild:[AdminRootGuard], loadChildren: () => import('../admin/master-management/master-management.module').then(m => m.MasterManagementModule) },
      { path: 'report-log', canActivateChild:[AdminRootGuard], loadChildren: () => import('./report-log/report-log.module').then(m => m.ReportLogModule) },
      { path: 'directory', canActivateChild:[AdminRootGuard], loadChildren: () => import('./directory/directory.module').then(m => m.DirectoryModule) },
      { path: 'tickets', canActivateChild:[AdminRootGuard], loadChildren: () => import('./ticket/ticket.module').then(m => m.TicketModule) },
      { path: 'shop', canActivateChild:[AdminRootGuard], loadChildren: () => import('./shop/shop.module').then(m => m.ShopModule) },
      { path: 'fileservice', canActivateChild:[AdminRootGuard], loadChildren: () => import('./fileservice/fileservice.module').then(m => m.FileserviceModule) },
      { path: 'vehicle-management', canActivateChild:[AdminRootGuard], loadChildren: () => import('./vehicle-management/vehicle-management.module').then(m => m.VehicleManagementModule) },
      { path: 'manual-handle', canActivateChild:[AdminRootGuard], loadChildren: () => import('./manual-handle/manual-handle.module').then(m => m.ManualHandleModule) },
      { path: 'knowledgebase', canActivateChild:[AdminRootGuard], loadChildren: () => import('./knowledge-base/knowledge-base.module').then(m => m.KnowledgeBaseModule) },
      { path: 'knowledge', canActivateChild:[AdminRootGuard], loadChildren: () => import('./new-knowledge-base/new-knowledge-base.module').then(m => m.NewKnowledgeBaseModule) },
      { path: 'manage-dashboard', canActivateChild:[AdminRootGuard], loadChildren: () => import('./manage-dashboard/manage-dashboard.module').then(m => m.ManageDashboardModule) },
    ]
  },
     
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AdminRoutingModule { }
