import { Component } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { CrudService } from 'src/app/services/crud.service';
import { NotificationsService } from 'src/services/notifications.service';

@Component({
  selector: 'app-tech-team-dash-table',
  templateUrl: './tech-team-dash-table.component.html',
  styleUrls: ['./tech-team-dash-table.component.scss'],
})
export class TechTeamDashTableComponent {
  fileservices: any;
  type: any = this.ar.queryParams;
  constructor(
    private crud: CrudService,
    public noti: NotificationsService,
    private toastr: ToastrService,
    private ar: ActivatedRoute
  ) {}
  ngOnInit() {
    if (this.type['_value'].chartType) {
      var url;
      // {{url}}/dashboard/internal/technical/file_by_model/data?month=12&filter_by=month&year=2023
      // {{url}}/dashboard/internal/technical/file_by_model/data?filter_by=date&start_date=2023-12-01&end_date=2023-12-01

      if (this.type['_value'].chartType == 'month') {
        url = `dashboard/internal/technical/file_by_model/data?month=${this.type['_value'].month}&filter_by=month&year=${this.type['_value'].year}`;
      } else {
        url = `dashboard/internal/technical/file_by_model/data?filter_by=date&start_date=${this.type['_value'].start_date}&end_date=${this.type['_value'].end_date}`;
      }
      this.crud.getRequest(`${url}`).then((res: any) => {
        if (res) {
          this.fileservices = res.data;
        }
      });
    }
  }
  downloadCsv(data: any[], fileName: string): void {
    const csvContent = this.constructCsvContent(data);
    const blob = new Blob([csvContent], { type: 'text/csv' });

    // Create link element to initiate download
    const a = document.createElement('a');
    a.href = window.URL.createObjectURL(blob);
    a.download = fileName;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
  }

  private constructCsvContent(data: any[]): string {
    let csvContent = 'Request ID,Created At,Vehicle Make,Model\n';

    // Loop through the data and construct CSV rows
    data.forEach((row) => {
      csvContent += `${row.request_id},${this.noti.formatDate(
        row?.created_at
      )} ,${row.vehicle_make},${row?.model || 'NA'}\n`;
    });

    return csvContent;
  }
}
