import { Component } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { CrudService } from 'src/app/services/crud.service';
import { NotificationsService } from 'src/services/notifications.service';

@Component({
  selector: 'app-ave-file-tat-table',
  templateUrl: './ave-file-tat-table.component.html',
  styleUrls: ['./ave-file-tat-table.component.scss'],
})
export class AveFileTatTableComponent {
  fileservices: any;
  type: any = this.ar.queryParams;
  constructor(
    private crud: CrudService,
    public noti: NotificationsService,
    private toastr: ToastrService,
    private ar: ActivatedRoute
  ) {}
  ngOnInit() {
    if (this.type['_value'].chartType) {
      var url;
      if (this.type['_value'].chartType == 'month') {
        url = `dashboard/internal/technical/month/data?filter_by=month&month_number=${this.type['_value'].month}&year=${this.type['_value'].year}`;
      } else {
        url = `dashboard/internal/technical/month/data?filter_by=date&start_date=${this.type['_value'].start_date}&end_date=${this.type['_value'].end_date}`;
      }
      this.crud.getRequest(`${url}`).then((res: any) => {
        if (res) {
          this.fileservices = res.data;
        }
      });
    }
  }
  downloadCsv(data: any[], fileName: string): void {
    const csvContent = this.constructCsvContent(data);
    const blob = new Blob([csvContent], { type: 'text/csv' });

    // Create link element to initiate download
    const a = document.createElement('a');
    a.href = window.URL.createObjectURL(blob);
    a.download = fileName;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
  }

  private constructCsvContent(data: any[]): string {
    let csvContent = 'RequestId,Start time,End time,Differences (Seconds)\n';

    // Loop through the data and construct CSV rows
    data.forEach((row) => {
      const requestId = `FR${row.file_request}`; // Adding "FR" prefix to id
      const startTime = this.noti.formatDate(row?.start_time);
      const endTime = this.noti.formatDate(row?.end_time);
      const difference = row.second_diff;

      csvContent += `${requestId},${startTime},${endTime},${difference}\n`;
    });

    return csvContent;
  }
}
