
import { AfterViewInit, Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { encryption } from 'src/app/encryption';
import { RoleService } from '../services/role.service';
import { ToastrService } from 'ngx-toastr';
import { Title } from '@angular/platform-browser';
import { NotificationsService } from 'src/services/notifications.service';
import { PermissionsService } from '../services/permissions.service';
import { CrudService } from 'src/app/services/crud.service';
import { requestUrl } from 'src/app/services/adminReqUrl.const';
import { SessionService } from 'src/app/services/session.service';
import { countryDetails } from 'src/app/global-files/global.const';

@Component({
  selector: 'app-admin-profile',
  templateUrl: './admin-profile.component.html',
  styleUrls: ['./admin-profile.component.scss']
})
export class AdminProfileComponent implements OnInit, AfterViewInit {
  permissions: any;
  tab: any = 'profile';
  changePasswordForm!: FormGroup;
  chnageEmailForm!: FormGroup;
  usersdetail: any;
  showOtpBox: boolean = false;
  profileForm: FormGroup;
  addphoneerror: boolean = true;
  image: any = '';
  formData: any = new FormData();
  countries = require('country-data-list').countries;
  textType: any = 'password'
  textType2: any = 'password'
  textType3: any = 'password'
  countryData= countryDetails


  constructor(private fb: FormBuilder, private sessionService: SessionService,
    private _roleservice: RoleService, private notification: NotificationsService,
    private toastr: ToastrService, private crud: CrudService, private _titleservice: Title,
    private _permissionservice: PermissionsService
  ) {
    this._titleservice.setTitle('My Profile');
    this.permissions = this._permissionservice.getPermissions();
    this.changePasswordForm = this.fb.group({
      current_password: ['', Validators.required],
      new_password: ['', [Validators.required, Validators.pattern(/^(?=.*[A-Z])(?=.*[a-z])(?=.*[0-9])(?=.*[!@#\$%\^&\*])(?=.{8,})/)]],
      confirm_password: ['', [Validators.required, Validators.pattern(/^(?=.*[A-Z])(?=.*[a-z])(?=.*[0-9])(?=.*[!@#\$%\^&\*])(?=.{8,})/)]]
    });
    this.profileForm = this.fb.group({
      first_name: ['', Validators.required],
      last_name: ['', Validators.required],
      address: ['', Validators.required],
      country: ['', Validators.required],
      phone_no: ['44', Validators.required],
      country_code:[''],
      email: ['', [Validators.required, Validators.email]]
    });
    if(this.permissions?.my_profile != 'Full access') {
      this.profileForm.disable();
    }
    this.chnageEmailForm = this.fb.group({
      email: ['', [Validators.required, Validators.email]],
      otp: ['']
    });
  }
  obj: any = [
    { "alpha2": "US", "name": "United States", },
    { "alpha2": "GB", "name": "United Kingdom", }
  ]
  telOptions = {
    separateDialCode:true,
    initialCountry:'GB'
  }

  @ViewChild('phoneInput') phoneInput!:ElementRef;
  
  ngOnInit(): void {
    this.countryData.sort((a: any, b: any) => (a.country > b.country) ? 1 : -1)
    this.obj.forEach((element: any) => {
      this.moveCountryOnTop(element.name);
    });
    this.usersdetail = JSON.parse(localStorage.getItem('userdetails') || '');

    this.image = this.usersdetail?.image;
    
    let profileData = {
      "first_name": this.usersdetail?.first_name,
      "last_name": this.usersdetail?.last_name,
      "address": this.usersdetail?.address_user?.address,
      "country": this.usersdetail?.address_user?.country,
      "email": this.usersdetail?.email,
    };


    this.profileForm.patchValue(profileData);
  }
  
  ngAfterViewInit(): void {
    this.setPhoneNumber(this.usersdetail?.phone_no, this.usersdetail?.country_code)
  }

  moveCountryOnTop(name: string) {
    const index = this.countryData.findIndex((item: any) => item.country === name);
    if (index !== -1) {
      const removedObject = this.countryData.splice(index, 1)[0];
      this.countryData.unshift(removedObject);
    }
  }

  chnagePasswordSubmit() {
    if (this.changePasswordForm.valid) {
      let adminBody = this.changePasswordForm.value;
      adminBody['current_password'] = encryption(this.changePasswordForm.controls['current_password'].value);
      adminBody['new_password'] = encryption(this.changePasswordForm.controls['new_password'].value);
      this.crud.postRequest(`${requestUrl.adminChangePswd}`, adminBody).then((res: any) => {
        if (res)
          this.toastr.success(res.message);
      });
    }
    else {
      this.markAllControlsAsTouched(this.changePasswordForm);
    }
  }
  markAllControlsAsTouched(formGroup: FormGroup) {
    Object.values(formGroup.controls).forEach(control => {
      control.markAsTouched();
      if (control instanceof FormGroup) {
        this.markAllControlsAsTouched(control);
      }
    });
  }


  chnageEmailSubmit() {
    if(this.chnageEmailForm.valid){
    this._roleservice.updateEmail(this.chnageEmailForm.value).subscribe((res: any) => {
      this.showOtpBox = true;
      this.toastr.success(res.message, '', { timeOut: 4000 });
    },
      (err: any) => {

        this.toastr.error(err.error.detail, '', { timeOut: 4000 });
      });
    }
    else{
      this.markAllControlsAsTouched(this.chnageEmailForm);
    }
  }


  updateEmailRequest() {
    let body = {
      email: this.chnageEmailForm.value.email,
      otp: this.chnageEmailForm.value.otp,
      uuid: this.usersdetail.uuid
    }
    this._roleservice.updateRequest(body).subscribe((res: any) => {
      this.toastr.success(res.message, '', { timeOut: 4000 });
      this.sessionService.replaceKey('email', body.email);
      this.profileForm.controls['email'].setValue(body.email);
    },
      (err: any) => {
        this.toastr.error(err.error.detail, '', { timeOut: 4000 });
      });
  }

  setCountryCode(event: any) {
    console.log('change country');
    
    this.profileForm.patchValue({ 'country_code': event.dialCode });
  }

  setPhoneNumber(phone:string, countryCode:string) {
    let number = ""
    if(countryCode && !phone.includes(countryCode)) {
      number = `+${countryCode}${phone}`;
    } else {
      number = phone;
    }

    this.profileForm.get("phone_no")?.setValue(number);

    // ng2-tel-input sometimes does not automatically update selected country
    this.phoneInput.nativeElement.dispatchEvent(
      new KeyboardEvent('keyup', { bubbles: true })
    );
  }


  getPhoneNumber() {
    const phone:string = this.profileForm.controls['phone_no'].value;
    const countryCode = this.profileForm.controls['country_code'].value;
    if(countryCode && phone.includes("+" + countryCode)) {
      return phone.replace("+" + countryCode, "");
    }
    return phone;
  }

  profileFormSubmit() {
    
    if (this.profileForm.valid) {
      this.formData.append('first_name', this.profileForm.controls['first_name'].value);
      this.formData.append('last_name', this.profileForm.controls['last_name'].value);
      this.formData.append('country', this.profileForm.controls['country'].value);
      this.formData.append('address', this.profileForm.controls['address'].value);
      this.formData.append('phone_no', this.getPhoneNumber());
      this.formData.append('country_code', this.profileForm.controls['country_code'].value);

      this.crud.postRequest(`${requestUrl.postAdminProfile}`, this.formData).then((res: any) => {
        if (res) {
          this.formData = new FormData();
          this.toastr.success(res.message);
          this.usersdetail['first_name'] = this.profileForm.controls['first_name'].value;
          this.usersdetail['last_name'] = this.profileForm.controls['last_name'].value;
          this.usersdetail['address_user'] = {
            "address": this.profileForm.controls['address'].value,
            "country": this.profileForm.controls['country'].value,
          }
          this.usersdetail['phone_no'] = this.profileForm.controls['phone_no'].value;
          this.usersdetail['country_code'] = this.profileForm.controls['country_code'].value;
          this.usersdetail['image'] = res.image;
          localStorage.setItem('userdetails', JSON.stringify(this.usersdetail));
          this.notification.updateProfileImg(true);
        }
      });
    } else {
      this.markAllControlsAsTouched(this.profileForm);
    }
  }


  addphoneError(obj: any) {
    this.addphoneerror = obj;
    if (obj) {
      this.profileForm.controls['phone_no'].setErrors(null);
    } else {
      this.profileForm.controls['phone_no'].setErrors({ 'incorrect': false });
    }
  }



  onFileChange(event: any) {
    if (event.target.files && event.target.files[0]) {
      const file = event.target.files[0];
      const reader = new FileReader();
      reader.onload = e => this.image = reader.result;
      reader.readAsDataURL(file);
      this.formData.append("image", file);
      this.formData.append("uuid", this.usersdetail?.uuid);
      this.updateProfileImg();
    }
  }

  updateProfileImg() {
    this.crud.postRequest(`${requestUrl.updateProfileImg}`, this.formData).then((res: any) => {
      if (res) {
        this.usersdetail['image'] = res.url;
        localStorage.setItem('userdetails', JSON.stringify(this.usersdetail));
        this.notification.updateProfileImg(true);
      }
    })
  }
  resetData() {
    this.changePasswordForm.reset();
  }
}


