import { Component, HostListener, OnInit } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { NotificationsService } from 'src/services/notifications.service';
import { PermissionsService } from './services/permissions.service';
import { CrudService } from '../services/crud.service';
import { requestUrl } from '../services/adminReqUrl.const';
import { HttpClient, HttpClientModule } from '@angular/common/http';
import { XorEncryptionService } from '../services/xor-encryption.service';

@Component({
  selector: 'app-admin',
  templateUrl: './admin.component.html',
  styleUrls: ['./admin.component.scss']
})
export class AdminComponent implements OnInit {
  isDesktopMenuOpen: boolean = true;
  ismenuShow: boolean = true;
  windowWidth: any;
  credit: boolean = false;
  myfile: boolean = false;
  support: boolean = false;
  usersdetail: any;
  permissions: any;
  role_title: any;
  image: string = '';
  page: number = 1;
  pageSize: number = 10;
  unseenNotiCount: number = 0;
  notificationList: any[] = [];
  throttle = 1;
  distance = 2;
  vehMgmt: boolean = false;
  manHandle: boolean = false;
  showSync: boolean = false;
  showArticleUpdate: boolean = false;
  showSyncMsg: string = '';
  showArticleUpdateMsg: string = '';
  constructor(
    private router: Router,
    private titleservice: Title, public notification: NotificationsService,
    private _permissions: PermissionsService,
    private crud: CrudService,
    private http: HttpClient,
    private xorEncryption:XorEncryptionService,
  ) {
    this.titleservice.setTitle('Admin');
    this.permissions = this._permissions.getPermissions();
  }
  permission: any = {}
  ngOnInit(): void {
    this.windowWidth = window.innerWidth;
    this.ismenuShow = (window.screen.width <= 767) ? false : true;
    this.usersdetail = JSON.parse(localStorage.getItem('userdetails') || '');
    this.notification.initSocket();
    this.notification.onMessageAdmin.subscribe((res: any) => {
      if (res && res.type === 'admin_permission_update' && this.usersdetail.role != 'AIREMAP_ADMIN') {
        res.data.map((key: any) => {
          this.permission[key.permission] = key.access;
        })
        localStorage.setItem('permissions', JSON.stringify(this.permission));
        this.permissions = this._permissions.getPermissions();
      }
      else if (res && res.type === 'admin_new_notification') {
        this.unseenNotiCount = res.data.un_seen_message_count
        this.notificationList.unshift(res.data)
      }
      else if (res && res.type === 'admin_file_assigned') {
        this.unseenNotiCount = 1;
        let body = {
          message: res.message,
          type: res.type,
          id: res.ids,
          created_at: res.created_at
        }
        this.notificationList.unshift(body);
      }
      else if (res && res.type === 'admin_ticket_notification_technical_team') {
        if (res.uuid == this.usersdetail.uuid) {
          this.unseenNotiCount = 1;
          let body = {
            message: res.message,
            type: res.type,
            id: res.ids,
            created_at: res.created_at
          }
          this.notificationList.unshift(body);
        }
      }
      // else if (res && res.type === 'admin_slave_file_completed' || res.type === 'admin_master_file_completed') {
      //   this.unseenNotiCount = 1;
      //   let body = {
      //     message: res.message,
      //     type: res.type,
      //     id: res.ids,
      //     created_at: res.created_at
      //   }
      //   this.notificationList.unshift(body);
      // }
      else if (res && res.type === 'admin_cloud_sync_completed') {
        this.showSync = true;
        this.showSyncMsg = res.message;
      }
      else if (res && res.type === 'admin_zoho_sync_completed') {
        this.showArticleUpdate = true;
        this.showArticleUpdateMsg = res.message;
      }
    });

    this.image = this.usersdetail.image;
    this.role_title = this.usersdetail.roles.name;
    this.notification.updateImg.subscribe(res => {
      if (res) {
        this.usersdetail = JSON.parse(localStorage.getItem('userdetails') || '');
        this.image = this.usersdetail.image;
      }
    });
    this.getNotification();
  }
  getNotification() {
    this.crud.getRequest(`${requestUrl.getNotification}${this.page}&page_size=${this.pageSize}`,true).then((res: any) => {
      if (res) {
        this.notificationList = this.notificationList.concat(res.data.results);
        this.unseenNotiCount = res.data.un_seen_message_count;
      }
    })
  }

  logout() {
    localStorage.removeItem('userdetails');
    localStorage.removeItem('token');
    localStorage.removeItem('permissions');
    localStorage.removeItem('dashBoardPermissions');
    this.router.navigate(['/']);
  }

  @HostListener('window:resize', ['$event'])
  onWindowResize() {
    this.windowWidth = window.innerWidth;
    this.ismenuShow = (window.screen.width <= 767) ? false : true;
  }
  
  toggleSidebar = false;
  toggleMenu() {
    this.toggleSidebar = !this.toggleSidebar;
  }
  toggleMenuOnProfile() {
    this.windowWidth = window.innerWidth;
    if (window.screen.width <= 767)
      this.ismenuShow = false;
  }
  redirectTo(data: any) {
    if (data.type == 'admin_slave_file_completed' || data.type == 'admin_master_file_completed')
      this.router.navigateByUrl(`admin/fileservice/view/${data.id.replace('FR', '')}`)
    else if (data.type == 'admin_ticket_notification_technical_team')
      this.router.navigateByUrl(`/admin/tickets/view-ticket/${data.id}/${data.id.slice(2, 5)}`)
    else if (data.ticket) {
      this.router.navigateByUrl(`/admin/tickets/view-ticket/${data.ticket.ids}/${data.ticket.id}`)
    } else if (data.type == 'admin_file_assigned') {
      this.router.navigateByUrl(`admin/fileservice`)
    }
  }

  // Encrypted
  // redirectTo(data: any) {
  //   if (data.type == 'admin_slave_file_completed' || data.type == 'admin_master_file_completed')
  //     this.router.navigateByUrl(`admin/fileservice/view/${data.id.replace('FR', '')}`)
  //   else if (data.type == 'admin_ticket_notification_technical_team')
  //     this.router.navigateByUrl(`/admin/tickets/view-ticket/${this.xorEncryption.xorEncryptDecrypt(data.id)}/${this.xorEncryption.xorEncryptDecrypt(data.id.slice(2, 5))}`);
  //   else if (data.ticket) {
  //     this.router.navigateByUrl(`/admin/tickets/view-ticket/${this.xorEncryption.xorEncryptDecrypt(data.ticket.ids)}/${this.xorEncryption.xorEncryptDecrypt(data.ticket.id)}`);
  //   } else if (data.type == 'admin_file_assigned') {
  //     this.router.navigateByUrl(`admin/fileservice`)
  //   }
  // }

  readNotification() {
    this.crud.postRequest(requestUrl.readNotification, {}).then((res: any) => {
      if (res)
        this.unseenNotiCount = 0;
    })
  }
  onScroll() {
    this.page = this.page + 1;
    this.getNotification();
  }

  toggleTopMenu() {
    this.isDesktopMenuOpen = !this.isDesktopMenuOpen;

  }
  openLink() {
    // Update one on one redirect url
    window.open('https://filemakersystemfront.influentiator.com/', '_blank');
  }
}
