import { Component } from '@angular/core';
import { Chart } from 'chart.js';
import { requestUrl } from 'src/app/services/adminReqUrl.const';
import { CrudService } from 'src/app/services/crud.service';

@Component({
  selector: 'app-management-team-dashboard',
  templateUrl: './management-team-dashboard.component.html',
  styleUrls: ['./management-team-dashboard.component.scss']
})
export class ManagementTeamDashboardComponent {

  modelChart: any;
  creditPurchaseData: any;
  supportTicketChart: any;
  titleSize = 15;
  constructor(private crud: CrudService) { 
    Chart.defaults.color = '#000000e6';
    Chart.defaults.font.size = 14;
  }
  ngOnInit() {
    this.staticChartData();
    this.getCreditPurchanse();
    this.dealerPerformance();
  }
  getCreditPurchanse() {
    this.crud.getRequest(`${requestUrl.getCreditPurchased}`).then((res: any) => {
      if (res) {
        this.creditPurchaseData = res.data;
        this.modelChart = new Chart('creditPurchased', {
          type: 'doughnut',
          data: {
            labels: ['EVC Credit', 'File Key Credits', 'Function Credits'],
            datasets: [
              {
                label: '',
                data: [
                  this.creditPurchaseData.credit_purchased.evc_credit || 0, 
                  this.creditPurchaseData.credit_purchased.file_credit || 0,
                  this.creditPurchaseData.credit_purchased.function_credit || 0
                ],
                backgroundColor: ['darkblue ', 'brown', 'lightgreen'],
                hoverOffset: 4,
              },
            ],
          },
          options: {
            responsive: true,
            maintainAspectRatio: true,
          },
        });
        // Credit Added Chart
        this.modelChart = new Chart('creditAdded', {
          type: 'doughnut',

          data: {
            labels: ['EVC Credits Added', 'File Key Credits Added', 'Function Credits Added'],
            datasets: [
              {
                label: '',
                data: [this.creditPurchaseData.credit_manual_added.evc_credit, this.creditPurchaseData.credit_manual_added.file_credit,
                this.creditPurchaseData.credit_manual_added.function_credit],
                backgroundColor: ['darkblue ', 'brown', 'lightgreen'],
                hoverOffset: 4,
              },
            ],
          },

          options: {
            responsive: true,
            maintainAspectRatio: true,
          },
        });
        // Credit Subracted Chart
        this.modelChart = new Chart('creditSubtracted', {
          type: 'doughnut',

          data: {
            labels: ['EVC Credits Subtracted', 'File Key Credits Subtracted', 'Function Credits Subtracted'],
            datasets: [
              {
                label: '',
                data: [this.creditPurchaseData.credit_manual_added.evc_credit, this.creditPurchaseData.credit_manual_added.file_credit,
                  this.creditPurchaseData.credit_manual_added.function_credit],
                backgroundColor: ['darkblue ', 'brown', 'lightgreen'],
                hoverOffset: 4,
              },
            ],
          },

          options: {
            responsive: true,
            maintainAspectRatio: true,
          },
        });
      }

    });
  }
  dealerPerformance() {
    this.crud.getRequest(`${requestUrl.getDealerPerformance}`).then((res: any) => {
      const inputData = res.data;

      const labels = inputData.map((entry: any) => entry.name);
      const evcCredit = inputData.map((entry: any) => entry.evc_credit);
      const fileCredit = inputData.map((entry: any) => entry.file_credit);
      const functionCredit = inputData.map((entry: any) => entry.function_credit);


      const data = {
        labels: labels,
        datasets: [
          {
            label: "File Credit",
            backgroundColor: "blue",
            data: fileCredit,
          },
          {
            label: "EVC Credit",
            backgroundColor: "green",
            data: evcCredit,
          },
          {
            label: "Function Credit",
            backgroundColor: "green",
            data: functionCredit,
          },
        ],
      };
      this.supportTicketChart = new Chart("support-ticket-chart", {
        type: 'bar',
        data: data,
        options: {
          plugins: {
            title: {
              display: false,
              font:{ size: this.titleSize },
              text: 'Chart.js Bar Chart - Stacked'
            },
          },
          responsive: true,
          scales: {
            x: {
              title: {
                display: true,
                font:{ size: this.titleSize },
                text: `Subdealer's name`,
              },
              stacked: true,
            },
            y: {
              title: {
                display: true,
                font:{ size: this.titleSize },
                text: 'Numbers of credit used',
              },
              stacked: true
            }
          }
        }
      });
    });
  }
  staticChartData() {
    this.modelChart = new Chart('creditRefunded', {
      type: 'doughnut',

      data: {
        labels: ['EVC Credits Refunded', 'File Key Credits Refunded', 'Function Credits Refunded'],
        datasets: [
          {
            label: '',
            data: [9, 3, 11],
            backgroundColor: ['darkblue ', 'brown', 'lightgreen'],
            hoverOffset: 4,
          },
        ],
      },

      options: {
        responsive: true,
        maintainAspectRatio: true,
      },
    });
  }

}
