  isFileTypeFetched = false;
  getCategoryChart(dateRange?: any) {
    // vehicle_tuned/category?range=3
    if (this.dashboardPermission?.FTMY) {
      this.VPERF1 = []; this.NO_TUNE = []; this.VBLUE_ECO = []; this.VBLEND_STAGE_05 = [];
      this.VRACE_STAGE_2 = []; this.ECOME = []; this.FUSIONME = []; this.PARKME = []; this.TCU_Gearbox = [];
      this.THRILLME = []; this.EXCITEME = []; this.BURBLEME = []; this.POPBANG_STEALTH_MODE = [];
      var url;
      if (this.selectedRangeFTMonthYear == -1)
        url = `dashboard/internal/technical/vehicle_tuned/category?start_date=${dateRange.startDate}&end_date=${dateRange.endDate}&filter_by=date`;
      else
        url = `dashboard/internal/technical/vehicle_tuned/category?range=${this.selectedRangeFTMonthYear}&filter_by=range`
      this.crud.get(url).subscribe((res: any) => {
        if (res) {
          try {
            const labels = res?.data?.map((entry: any) => entry.month);
            this.isFileTypeFetched = true;
            this.drawFileTypeGraph(res)
          }
          catch (error) {
            console.error('Chart could not be created:', error);
            // You can perform error handling here (e.g., display an error message)
          }

        }
      })
    }

  }


  getSupportTicketChart(dateRange?: any) {
    if (this.dashboardPermission?.TTDSTA) {
      let url = 'dashboard/internal/ticket/by_status/activity';
      if (this.selectedRangeTechTeamDashSupportActivity == -1)
        url += `?filter_by=date&start_date=${dateRange.startDate}&end_date=${dateRange.endDate}`;
      else {
        url += `?range=${this.selectedRangeTechTeamDashSupportActivity}&filter_by=range&year=${this.year}`
      }

      this.crud.get(url).subscribe((res: any) => {
        if (res) {
          this.drawSupportGraph(res)
        }
      })
    }
  }

  @ViewChild('reportFilesHandling') reportFilesHandling!:LineGraphComponent;
  getReportFileHnadlingChart(dateRange?: any) {
    if (this.dashboardPermission?.FMPRFMH) {
      var url;
      if (dateRange)
        url = `dashboard/internal/file_request/success/number?filter_by=date&start_date=${dateRange.startDate}&end_date=${dateRange.endDate}`;
      else {
        url = `dashboard/internal/file_request/success/number?month=${this.successMHMonth}&filter_by=month&year=${this.year}`
      }
      this.crud.get(url).subscribe((res: any) => {
        if (res) {
          let keys = Object.keys(res.data);
          let keysArray = keys.map((r: any) => r.replace(/_/g, ' '))
          const valuesArray: any = Object.values(res.data);
          this.reportFilesHandling.reDraw([{name:"",data:valuesArray}], this.transformLables(keysArray))
        }
      })
    }
  }

