  @ViewChild('fileRequestByType') fileRequestByType!:LineGraphComponent;
  @ViewChild('fileRequestByTypePie') fileRequestByTypePie!:LineGraphComponent;
  getFileRequestCharts(dateRange?: any) {

    if (this.dashboardPermission?.TTDFRT) {
      var url;
      if (dateRange)
        url = `dashboard/internal/file_request_by/category_and_credit?start_date=${dateRange.startDate}&end_date=${dateRange.endDate}&filter_by=date`;
      else {
        url = `dashboard/internal/file_request_by/category_and_credit?month=${this.FRTypeMonth}&year=${this.year}&filter_by=month`
      }
      this.crud.get(url).subscribe((res: any) => {
        if (res) {
          const keysArray = Object.keys(res.data?.credit_usage);

          const valuesArray = Object.values(res.data?.credit_usage).map(el => el || 0);
          let label = res.data?.files_by_category.map((r: any) => r.tuning_required)
          let count = res.data?.files_by_category.map((r: any) => r.count)
          this.fileRequestByType.reDraw([{name:"",data:count}], label)

          const doughtnutLables = keysArray.map(el => (el.charAt(0).toUpperCase() + el.slice(1)).replace('_',' '));
          this.fileRequestByTypePie.reDraw(valuesArray, doughtnutLables)
        }

      })
    }
  }



