  getAverageFileTurnData(type: any, dateRange?: any) {
    if (this.dashboardPermission?.AFTT) {
      this.fileTurnAroundData = []
      var url;
      if (type == 'month')
        url = `${URLS.getAdminFileTurnAroundTime}filter_by=month&month_number=${this.AFTTMonth}&year=${this.AFTTYear}`
      else {
        if (this.selectedRangeAverageFileTurnaroundTime == -1)
          url = `${URLS.getAdminFileTurnAroundTime}filter_by=date&start_date=${dateRange.startDate}&end_date=${dateRange.endDate}`;
        else {
          this.selectedRangeAverageFileTurnaroundTime = Number.isInteger(this.selectedRangeAverageFileTurnaroundTime) ? this.selectedRangeAverageFileTurnaroundTime : 6;
          url = `${URLS.getAdminFileTurnAroundTime}filter_by=range&range=${this.selectedRangeAverageFileTurnaroundTime}&year=${this.year}`
        }
      }
      this.crud.get(url).subscribe((response: any) => {
        if (response && response.data) {
          this.fileTurnAroundData = response.data?.tat_by_month;
          this.drawChart();
        }
      })
    }
  }
