  @ViewChild('manualHandling') manualHandling!:LineGraphComponent
  staticChartData(dateRange?: any) {
    var url;
    if (dateRange)
      url = `dashboard/internal/file_request_by/average/time?start_date=${dateRange.startDate}&end_date=${dateRange.endDate}&filter_by=date`;
    else {
      url = `dashboard/internal/file_request_by/average/time?month=${this.fileProdMonth}&filter_by=month&year=${this.year}`
    }
    this.crud.get(url).subscribe((res: any) => {
      if (res) {
        const labels = res.data.map((data: any) => data.tuning_required.replace(/_/g, ''));
        const values: any = res.data.map((data: any) => data.client.toFixed(2))
        const valuesMaster = res.data.map((data: any) => data.master.toFixed(2))


        const dataSets = [
          {
            name: 'Clients',
            data: values,
            borderWidth: 1,
            borderRadius: 3
          },
          {
            name: 'Master',
            data: valuesMaster,
            borderWidth: 1,
            borderRadius: 3
          },
        ]
        this.manualHandling.reDraw(dataSets, labels)
      }
    })
  }

