  isTopSubDealersLoading = false
  getTopSubDealer() {
    const start_date = `${this.currentYear}-${this.filterMonth}-1`;
    const end_date = `${this.currentYear}-${this.filterMonth}-${new Date(this.currentYear, this.filterMonth, 0).getDate()}`;
    this.isTopSubDealersLoading = true;
    this.adminService.getAdminTechnicalTeamData( start_date, end_date).subscribe((response) => {
      this.isTopSubDealersLoading = false;
      if (response && response.data) {
        this.getTeamData = response.data.top_sub_dealers;
        this.filterVisibleDate = `${new Date(this.currentYear, this.filterMonth, 0).toLocaleString('default', { month: 'long' })}, ${this.currentYear}`;
        if(this.getTeamData.length > 10) {
          this.getTeamData.length = 10;
        }
      }
    })
  }

  filterMonth = new Date().getMonth() + 1
  currentMonth = new Date().getMonth() + 1;
  currentYear = new Date().getFullYear();
  filterVisibleDate =  `${new Date().toLocaleString('default', { month: 'long' })}, ${this.currentYear}`;
  updateTopSubDealersFilter(flag:number) {
    if(this.filterMonth === 1 && flag === -1) {
      this.currentYear--;
      this.filterMonth = 12;
      this.getTopSubDealer();
      return;
    }
    if(this.filterMonth === 12 && flag === 1) {
      this.currentYear++;
      this.filterMonth = 1;
      this.getTopSubDealer();
      return;
    }
    this.filterMonth += flag;
    this.getTopSubDealer();
  }


