import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class RoleService {

  baseUrl = environment.baseUrl;
  constructor(private _http:HttpClient) 
  {


  }

  getRoles(params:any,disableLoader = false)
  {
    let headers = new HttpHeaders();
    if(disableLoader) {
      headers = headers.append('X-Disable-Loader','true')
    }
    return this._http.get(this.baseUrl+"/api/v1/admin/roles/", { params,headers });
  }

  createUser(data:any)
  {
    return this._http.post(this.baseUrl+'/api/v1/admin/roles/',data);
  }

  updateUser(data:any,id:any)
  {
    return this._http.put(`${this.baseUrl}/api/v1/admin/roles/${id}/`,data);
  }

  updateEmail(data:any)
  {
    return this._http.post(this.baseUrl+'/api/v1/account/update-email-request',data);
  }
  updateRequest(data:any)
  {
    return this._http.post(this.baseUrl+'/api/v1/account/update-email',data);
  }

  getUserByRole(id:any)
  {
    return this._http.get(this.baseUrl+`/api/v1/admin/users-by-role/${id}`);
  }

  ticketAssignTo(ticketid:any,data:any)
  {
    return this._http.post(this.baseUrl+`/api/v1/admin/assign-ticket-history/${ticketid}`,data);
  }

}
